.zzz-a-button{--button-color: var(--black);--bg-color: var(--white);--padding: 0.625rem 2.5rem;--border-radius: 0.5rem;--bg-hover-color: var(--wave-white);color:var(--button-color);background-color:var(--bg-color);padding:var(--padding);border-radius:var(--border-radius);border:none;cursor:pointer;display:inline-flex;text-decoration:none;position:relative}.zzz-a-button{font-family:"Work Sans",Helvetica,Arial,sans-serif;font-weight:400;font-size:1.25rem;line-height:1.25rem}.zzz-a-button::before{content:"";height:100%;width:0%;display:block;position:absolute;background-color:var(--bg-hover-color);top:0;left:50%;border-radius:var(--border-radius)}.zzz-a-button:hover{transform:scale(1)}.zzz-a-button:hover::before{z-index:-1;transition:400ms;width:100%;left:0}.zzz-a-button--btn-black{--button-color: var(--white);--bg-color: var(--black);--bg-hover-color: var(--wave-black)}.zzz-a-link-with-icon{--color: var(--white);--hover-color: var(--white);display:flex;color:var(--color)}.zzz-a-link-with-icon:hover{color:var(--hover-color)}.zzz-a-icon--md{width:1.875rem;height:1.875rem}.zzz-a-textarea label{font-family:"Work Sans",Helvetica,Arial,sans-serif;font-weight:700;font-size:1rem;line-height:1.25}.zzz-a-textarea label{font-weight:400;margin-bottom:1rem;display:block}.zzz-a-textarea textarea{font-family:"Work Sans",Helvetica,Arial,sans-serif;font-weight:700;font-size:1rem;line-height:1.25}.zzz-a-textarea textarea{font-weight:400;width:100%;background-color:var(--secondary);border:.0625rem solid var(--primary);border-radius:.3125rem;padding:.625rem .9375rem}.zzz-a-textarea textarea{min-height:4.6875rem}.zzz-a-text-input label{font-family:"Work Sans",Helvetica,Arial,sans-serif;font-weight:700;font-size:1rem;line-height:1.25}.zzz-a-text-input label{font-weight:400;margin-bottom:1rem;display:block}.zzz-a-text-input input{font-family:"Work Sans",Helvetica,Arial,sans-serif;font-weight:700;font-size:1rem;line-height:1.25}.zzz-a-text-input input{font-weight:400;width:100%;background-color:var(--secondary);border:.0625rem solid var(--primary);border-radius:.3125rem;padding:.625rem .9375rem}.zzz-a-error-message{margin-top:.625rem;color:var(--red)}.zzz-a-picture{display:flex}.zzz-a-picture img{height:auto;width:100%;object-fit:cover}.zzz-m-link-list-with-icon{padding:0;margin:0;list-style:none;display:flex;gap:.625rem}.zzz-m-price{display:grid;gap:.625rem;box-shadow:.1875rem .25rem .75rem .0625rem rgba(var(--primary-rgb), 0.25);padding:1.875rem 1.25rem;border-radius:.375rem;background-color:var(--primary-bg)}@media(min-width: 64rem){.zzz-m-price{padding:2.5rem}}.zzz-m-price__price{display:flex;gap:.625rem;align-items:center;justify-content:center}.zzz-m-price__price .value{font-family:"Crimson Text",serif;font-weight:700;font-size:2.5rem;line-height:1.25}.zzz-m-price__price .time{color:var(--grey)}.zzz-m-price__button{margin-top:.625rem}.zzz-m-price .zzz-m-list{margin-top:.625rem}.zzz-m-list{padding:0;margin:0;list-style:none;display:grid;gap:.625rem}.zzz-m-item{display:flex;align-items:center;justify-content:center;gap:.625rem}.zzz-m-item .zzz-a-icon{color:var(--subtitle)}